import React, { useState, useEffect } from "react"; // useEffect import 
import { DatePicker, ConfigProvider, Space } from "antd";
import koKR from "antd/lib/locale/ko_KR";
import dayjs from "dayjs";
import "./Calendar.css";
import "dayjs/locale/ko"; //     

dayjs.locale("ko"); //   

const { RangePicker } = DatePicker;

const Cal = () => {
  const [selectedDates, setSelectedDates] = useState([
    dayjs(),
    dayjs().add(1, "day"),
  ]); //    
  const [nights, setNights] = useState("");

  //       
  useEffect(() => {
    const start = selectedDates[0];
    const end = selectedDates[1];
    const nightsCount = end.diff(start, "day");
    setNights(`(${nightsCount})`);
  }, []); //       

  const handleDateChange = (dates) => {
    setSelectedDates(dates);
    if (dates && dates[0] && dates[1]) {
      const start = dates[0];
      const end = dates[1];
      const nightsCount = end.diff(start, "day");
      setNights(`(${nightsCount})`);
    } else {
      setNights(""); //       
    }
  };

  const today = dayjs();
  const tomorrow = dayjs().add(1, "day");
  const dateFormat = "MM.DD ddd";

  return (
    <ConfigProvider locale={koKR}>
      <Space direction="vertical" size={12}>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            borderRadius: "10px",
            border: "none",
            backgroundColor: "#f5f5f5",
          }}
        >
          <RangePicker
            defaultValue={[today, tomorrow]}
            format={dateFormat}
            style={{
              border: "none",
              backgroundColor: "#f5f5f5",
              borderRadius: "10px",
              height: "50px",
              width: "210px",
            }}
            separator={false}
            locale={koKR}
            dateRender={(current) => (
              <div className="ant-picker-cell-inner">{current.format("D")}</div>
            )}
            onChange={handleDateChange} // onChange   
          />
          <div
            style={{
              border: "none",
              backgroundColor: "#f5f5f5",
              display: "flex", // flex  
              justifyContent: "left", //     
              alignItems: "center", //     
              borderRadius: "10px",
              height: "50px",
              width: "60px",
              fontSize: "19px",
              fontWeight: "600",
            }}
          >
            {nights}
          </div>
        </div>
      </Space>
    </ConfigProvider>
  );
};

export default Cal;
