import React from "react";
import * as S from "../styles/RoomTypeStyle";
import SectionHeader from "../SectionHeader";
import room1 from "../../../images/room1.png";
import MoreBtn from "../../../components/Button/MoreBtn";
import PrimaryBtn from "../../../components/Button/PrimaryBtn";
import { useNavigate } from "react-router-dom";

const RoomType = ({ id }) => {

  const navigate = useNavigate();

  const NavigateToRes = () => {
    navigate("/reservation");
  };

  return (
    <article id={id}>
      <SectionHeader sectionTitle=" " />
      <S.RoomCard>
        <S.RoomTypeImage>
          <img src={room1} alt="" />
        </S.RoomTypeImage>
        <S.RoomTypeInfo>
          <S.RoomTypeName>
            <h3>  ( / Room Only)</h3>
            <MoreBtn buttonText="" />
          </S.RoomTypeName>
          <S.RoomTypeText>
            <div className="top_box box">
              <div className="time_info">
                <p> 16:00</p>
                <p> 11:00</p>
              </div>
              <div>
                <div className="price">113,600</div>
                <PrimaryBtn text=' ' onClick={NavigateToRes} />
              </div>
            </div>
            <div className="bottom_box box">
              <p className="sub_title"></p>
              <p className="sub_text">2  2</p>
            </div>
          </S.RoomTypeText>
        </S.RoomTypeInfo>
      </S.RoomCard>

      <S.RoomCard>
        <S.RoomTypeImage>
          <img src={room1} alt="" />
        </S.RoomTypeImage>
        <S.RoomTypeInfo>
          <S.RoomTypeName>
            <h3>  ( / Room Only)</h3>
            <MoreBtn buttonText="" />
          </S.RoomTypeName>
          <S.RoomTypeText>
            <div className="top_box box">
              <div className="time_info">
                <p> 16:00</p>
                <p> 11:00</p>
              </div>
              <div>
                <div className="price">113,600</div>
                <PrimaryBtn text=' ' onClick={NavigateToRes} />
              </div>
            </div>
            <div className="bottom_box box">
              <p className="sub_title"></p>
              <p className="sub_text">2  2</p>
            </div>
          </S.RoomTypeText>
        </S.RoomTypeInfo>
      </S.RoomCard>
    </article>
  );
};

export default RoomType;
