import React from "react";
import styled from "styled-components";

const Container = styled.div`
  display: grid;
  grid-template-columns: 130px repeat(6, 1fr) 130px;
  width: 100%; /*      */
  margin-top: 80px;
  background-color: #fafafa;
`;

const Item = styled.div`
  padding: 20px;
  height: 150px;
  margin-top: 30px;
  width: 230px;
  background-color: #fafafa;
`;

const SpannedItem = styled(Item)`
  grid-column: 2 / Span 3; /*  3    */
  width: 420px;
`;

const Content = styled.div`
  background-color: #fafafa;
  width: 100%;
  padding: 30px 145px 0 145px;
`;

const Btn = styled.button`
  background-color: #ffffff;
  border: 2px solid #ebebeb;
  border-radius: 6px;
  margin: 6px 9px 6px 0;
  padding: 10px;
`;

const Div = styled.div`
  font-size: 17px;
  color: gray;
  font-weight: 500;
  padding-bottom: 9px;
`;

const Div2 = styled.div`
  font-size: 19px;
  color: black;
  font-weight: 600;
  padding-bottom: 15px;
`;

const Div3 = styled.div`
  font-size: 13px;
  color: gray;
  font-weight: 400;
  padding-bottom: 6px;
`;

const Span = styled.span`
  padding-right: 15px;
  font-size: 16px;
  font-weight: 500;
  color: #9ca0a6;
`;

function Footer() {
  return (
    <>
      <Container>
        <SpannedItem>
          <Div2></Div2>
          <Div> 9 ~  3 </Div>
          <Btn>1670-6250</Btn>
          <Btn> </Btn>
        </SpannedItem>
        {/* 3    */}
        <Item>
          <Div2></Div2>
          <Div></Div>
        </Item>{" "}
        {/*    */}
        <Item>
          {" "}
          <Div2></Div2>
          <Div></Div>
          <Div>  </Div>
          <Div> /  </Div>
        </Item>{" "}
        {/*    */}
        <Item>
          <Div2></Div2>
          <Div>  </Div>
          <Div>   </Div>
          <Div>  </Div>
        </Item>
      </Container>

      <Content>
        <Div3>()</Div3>
        <Div3>
           :    479, 479 11 |  :
           |  : 742-86-00224
        </Div3>
        <Div3>
           : help@goodchoice.kr |  : 2017--01779
          |   : 1026-24 |  : 1670-6250 |
             : ()
        </Div3>
        <Div3>
          ()    ,
           ,         
          .
        </Div3>
        <Div style={{ marginTop: "25px" }}>
          <Span></Span>
          <Span style={{ color: "#4a4138" }}></Span>
          <Span>  </Span>
          <Span>  </Span>
        </Div>
        <Div3 style={{ paddingTop: "50px", paddingBottom: "70px" }}>
          <span>Copyright GC COMPANY Corp. All rights reserved.</span>
        </Div3>
      </Content>
    </>
  );
}
export default Footer;
