import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import "./hotellist.css";
import { fetchData } from "./action";
import { Link, useSearchParams } from "react-router-dom";

const Hotellist = () => {
  const dispatch = useDispatch();
  const data1 = useSelector((state) => state.data);
  const [filteredData, setFilteredData] = useState([]);

  // useSearchParams      
  const [searchParams] = useSearchParams();
  const searchTerm = searchParams.get("query");
  console.log("Search term:", searchTerm);

  useEffect(() => {
    if (!data1.length) {
      dispatch(fetchData());
    }
  }, [data1, dispatch]);

  useEffect(() => {
    if (searchTerm) {
      const searchTermLower = searchTerm.toLowerCase();
      const filtered = data1.filter((item) => {
        const isMatch = item.addr1.toLowerCase().includes(searchTermLower);
        console.log(`Address: ${item.addr1}, Is Match: ${isMatch}`);
        return isMatch;
      });
      setFilteredData(filtered);
      console.log("Filtered Data:", filtered); //   
    } else {
      setFilteredData(data1);
    }
  }, [data1, searchTerm]);

  //    
  const generateRandomPrice = () => {
    const price = Math.floor(Math.random() * (240000 - 60000 + 1)) + 60000; // 60,000 240,000   
    const roundedPrice = Math.round(price / 1000) * 1000; //    3 000 
    return `${roundedPrice.toLocaleString()}`; //    ""  
  };

  // const navigate = useNavigate();
  // const roomDetailClick = () => {
  //   navigate(`/detail_room`);
  // };
  return (
    <>
      <div className="container">
        <div className="options">
          {/*    */}
          <div className="option-group">
            <div
              style={{
                fontSize: "20px",
                fontWeight: "600",
                paddingBottom: "10px",
              }}
            >
              
            </div>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#OTT</button>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#</button>

            <button className="optionbutton">#</button>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#BBQ</button>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#</button>
            <button className="optionbutton">#</button>
          </div>
          <div className="option-group">
            {" "}
            <div
              style={{
                fontSize: "20px",
                fontWeight: "600",
                paddingBottom: "10px",
              }}
            >
              
            </div>
            <button className="optionbutton"></button>
            <button className="optionbutton"></button>
            <button className="optionbutton"></button>
          </div>
          <div className="option-group">
            {" "}
            <div
              style={{
                fontSize: "20px",
                fontWeight: "600",
                paddingBottom: "10px",
              }}
            >
              
            </div>
            <button className="optionbutton">5</button>
            <button className="optionbutton">4</button>
            <button className="optionbutton"></button>
            <button className="optionbutton"></button>
          </div>
        </div>
        <div className="hotelList">
          {/*   */}
          {filteredData.map(
            (item) =>
              //      
              item.firstimage && (
                <Link
                  key={item.id}
                  to={`/Detail_room/${item.title}`}
                  state={{
                    address: item.addr1,
                    mapx: item.mapx,
                    mapy: item.mapy,
                    img: item.firstimage
                  }}
                >
                  <div
                    className="hotelitem"
                  >
                    <img
                      className="hotelimage"
                      src={item.firstimage}
                      alt={item.title}
                    />
                    <div className="hotelinfo">
                      <div
                        style={{
                          fontSize: "22px",
                          fontWeight: "600",
                          paddingBottom: "10px",
                        }}
                      >
                        {item.title}
                      </div>
                      <div>{item.addr1}</div>
                      <div>{item.addr2}</div>
                      {/*    */}
                      <div
                        style={{
                          fontSize: "25px",
                          fontWeight: "600",
                          marginTop: "120px",
                        }}
                      >
                        {generateRandomPrice()}
                      </div>
                    </div>
                  </div>
                </Link>
              )
          )}

        </div>
      </div >
    </>
  );
};

export default Hotellist;
