import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./Signup.css";

const Signup = () => {
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [isButtonActive, setIsButtonActive] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    //       
    if (email && username && phoneNumber && password && confirmPassword) {
      setIsButtonActive(true);
    } else {
      setIsButtonActive(false);
    }
  }, [email, username, phoneNumber, password, confirmPassword]);

  const handleSignup = async (event) => {
    event.preventDefault();

    if (password !== confirmPassword) {
      alert("  .");
      return;
    }

    alert(" .");
    navigate("/login");
  };

  const navigateToLogin = () => {
    navigate("/login");
  };

  return (
    <div className="container">
      <div className="page">
        <form className="signup-form" onSubmit={handleSignup}>
          <h1 className="infomation">  </h1>
          <h2 className="info">     </h2>

          <div>
            <label className="email" htmlFor="email">
              
            </label>
          </div>
          <div>
            <input
              className="emailput"
              type="email"
              id="email"
              value={email}
              placeholder="abcd@naver.com"
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <div>
            <label className="username" htmlFor="username">
              
            </label>
          </div>
          <div>
            <input
              className="nameput"
              type="text"
              placeholder=""
              id="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
          </div>
          <div>
            <label className="phone-number" htmlFor="phone-number">
              
            </label>
          </div>
          <div>
            <input
              className="phoneput"
              type="text"
              id="phone-number"
              value={phoneNumber}
              placeholder="  "
              onChange={(e) => setPhoneNumber(e.target.value)}
            />
          </div>
          <div>
            <label className="password" htmlFor="password">
              
            </label>
          </div>
          <div>
            <input
              className="passwordput"
              type="password"
              id="password"
              value={password}
              placeholder=" 8 "
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>
          <div>
            <label className="confirm-password" htmlFor="confirm-password">
               
            </label>
          </div>
          <div>
            <input
              className="passwordfirmput"
              placeholder="   "
              type="password"
              id="confirm-password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            />
          </div>
          <div style={{ marginTop: "50px", marginBottom: "30px" }}>
            <button
              className={isButtonActive ? "check active" : "check"}
              id="signup-button"
              onClick={handleSignup}
              disabled={!isButtonActive}
            >
              
            </button>
          </div>

          <div
            className="login-link"
            onClick={navigateToLogin}
            style={{ display: "flex", justifyContent: "space-between" }}
          >
            <div> ?</div>
            <div></div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Signup;
