import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { ReactComponent as NavArrowRightIcon } from "../assets/Group 2.svg";
import { ReactComponent as NavArrowLeftIcon } from "../assets/Group 1.svg";
import Guam from "../assets/.webp";
import Kyoto from "../assets/.webp";
import Natrang from "../assets/.webp";
import Danang from "../assets/.webp";
import Tokyo from "../assets/.webp";
import Bangkok from "../assets/.webp";
import Singapore from "../assets/.webp";
import Osaka from "../assets/.webp";
import Kotakinabalu from "../assets/.webp";
import Fukuoka from "../assets/.webp";
import styled from "styled-components";
import "./Slider.css";

const ImageList = styled.div``;

const CityName = styled.p`
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  margin-top: 15px;
`;
function NextArrow(props) {
  const { className, style, onClick, currentSlide, slideCount } = props;

  return currentSlide < slideCount - 6 ? (
    <Arrow className={`right`} onClick={onClick}>
      <NavArrowRightIcon />
    </Arrow>
  ) : null;
}

function PrevArrow(props: any) {
  const { onClick, currentSlide, slideCount } = props;
  return currentSlide > 0 && currentSlide < slideCount ? (
    <Arrow className="left" onClick={onClick}>
      <NavArrowLeftIcon />
    </Arrow>
  ) : null;
}

export const SliderImg = styled.img`
  position: relative;
  width: 900px;
  height: 520px;
  border-radius: 4px;
`;

export const Arrow = styled.p`
  position: absolute;
  top: 43%;
  transform: translateY(-50%);

  color: #fff;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.3s;
  z-index: 1;
  &.left {
    left: -20px;
  }

  &.right {
    right: -20px;
  }
`;

function OverseasSlider() {
  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 4,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };
  return (
    <div className="slider-container" style={{ position: "relative" }}>
      <Slider {...settings} className="slider">
        <ImageList>
          <img src={Guam} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Kyoto} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Natrang} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Danang} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Tokyo} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Bangkok} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Singapore} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Osaka} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Kotakinabalu} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList>
          <img src={Fukuoka} alt="" />
          <CityName></CityName>
        </ImageList>
      </Slider>
    </div>
  );
}

export default OverseasSlider;
