import React from "react";
import styled from "styled-components";
import Slider from "react-slick";
import "./Slider.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { ReactComponent as NavArrowRightIcon } from "../assets/Group 2.svg";
import { ReactComponent as NavArrowLeftIcon } from "../assets/Group 1.svg";
import Busan from "../assets/.webp";
import Gapyeong from "../assets/.webp";
import Jeju from "../assets/.webp";
import Haewoondae from "../assets/.webp";
import Kangreung from "../assets/.webp";
import Sokcho from "../assets/.webp";
import Incheon from "../assets/.webp";
import Yeosoo from "../assets/.webp";
import Gyeongju from "../assets/.webp";
import Seoul from "../assets/.webp";
import { useNavigate } from "react-router-dom";

const ImageList = styled.div``;

const CityName = styled.p`
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  margin-top: 15px;
`;
function NextArrow(props) {
  const { className, style, onClick, currentSlide, slideCount } = props;

  return (
    //     .
    currentSlide < slideCount - 6 ? (
      <Arrow className={`right`} onClick={onClick}>
        <NavArrowRightIcon />
      </Arrow>
    ) : null
  );
}

function PrevArrow(props: any) {
  const { onClick, currentSlide, slideCount } = props;
  return (
    //    ,         .
    currentSlide > 0 && currentSlide < slideCount ? (
      <Arrow className="left" onClick={onClick}>
        <NavArrowLeftIcon />
      </Arrow>
    ) : null
  );
}

export const SliderImg = styled.img`
  position: relative;
  width: 900px;
  height: 520px;
  border-radius: 4px;
`;

export const Arrow = styled.p`
  position: absolute;
  top: 43%;
  transform: translateY(-50%);

  color: #fff;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.3s;
  z-index: 1;
  &.left {
    left: -20px;
  }

  &.right {
    right: -20px;
  }
`;

function DomesticSlider() {
  const navigate = useNavigate();
  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 4,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };

  const handleCityClick = (city) => {
    navigate(`/detail?query=${encodeURIComponent(city)}`);
  };

  return (
    <div className="slider-container" style={{ position: "relative" }}>
      <Slider {...settings} className="slider">
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Busan} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Gapyeong} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Haewoondae} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Gyeongju} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Sokcho} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Yeosoo} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Incheon} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Kangreung} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Jeju} alt="" />
          <CityName></CityName>
        </ImageList>
        <ImageList onClick={() => handleCityClick("")}>
          <img src={Seoul} alt="" />
          <CityName></CityName>
        </ImageList>
      </Slider>
    </div>
  );
}

export default DomesticSlider;