import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; // useNavigate 
import styled from "styled-components";

const Wrapper = styled.div`
  border: 1px solid #ebebeb;
  border-radius: 12px;
  padding-left: 24px;
  padding-right: 24px;
  width: 340px;
  display: flex;
  flex-direction: column;
  line-height: 4em;
  margin: 20px;
`;

const H4 = styled.h4`
  font-size: ${(props) => props.theme.fontSize_xl};
  font-weight: bold;
`;

const P = styled.p`
  font-size: ${(props) => props.theme.fontSize_md};
  color: ${(props) => props.theme.black};
`;

const Hr = styled.hr`
  background: #f5f5f5;
  border: 0;
  height: 2px;
  width: 100%;
`;

const Button = styled.button`
  width: 100%;
  height: 70px;
  margin-bottom: 20px;
  padding-bottom: 70px;
  border-style: none;
  border-radius: 10px;
  font-size: 18px;
  font-weight: bold;
  background-color: ${(props) => props.theme.blue};
  color: white;
`;

const A = styled.a`
  font-size: ${(props) => props.theme.fontSize_md};
  color: ${(props) => props.theme.black};
`;

function Terms() {
  const [termsBoolean, setTermsBoolean] = useState(false);
  const navigate = useNavigate(); // useNavigate  

  const showTerms = () => {
    setTermsBoolean(!termsBoolean);
  };

  const checkedHandler = () => {
    setTermsBoolean(!termsBoolean);
  };

  return (
    <>
      <div>
        <input onClick={checkedHandler} type="checkbox"></input>
        <A
          onClick={showTerms}
          style={{ fontWeight: "bold", cursor: "pointer" }}
        >
            
        </A>
      </div>
      {termsBoolean && <TermsList />}
    </>
  );
}

function TermsList() {
  return (
    <>
      <div style={{ lineHeight: "2em" }}>
        <div>
          <input id="1" type="checkbox"></input>
          <A>   / ()</A>
        </div>
        <Hr />
        <div>
          <input id="2" type="checkbox"></input>
          <A>    ()</A>
        </div>
        <Hr />
        <div>
          <input id="3" type="checkbox"></input>
          <A> 3  ()</A>
        </div>
        <Hr />
        <div>
          <input id="4" type="checkbox"></input>
          <A> 14  ()</A>
        </div>
        <Hr />
      </div>
    </>
  );
}

function PaymentsInfo() {
  const navigate = useNavigate(); // useNavigate  

  const handlePayment = () => {
    //     
    alert(" .");
    navigate("/home"); // useNavigate  "/home" 
  };

  return (
    <Wrapper>
      <Terms />
      <H4> </H4>
      <P style={{ fontWeight: "lighter" }}> </P>
      <p>113,000</p>
      <P style={{ fontWeight: "bold" }}>  </P>
      <p>113,000</p>
      <Button onClick={handlePayment}></Button>{" "}
      {/*   handlePayment   */}
    </Wrapper>
  );
}

export default PaymentsInfo;
